import React from 'react';
import './photoPage.css';



export const PhotoPage = class PhotoPage extends React.Component{
    render() {
        return (

            <div>
                <div className='h1'>
                    <h1>Some photo</h1>
                </div>
                <div className='photos'>
                    
                    <img className='photo' src ='1.jpg'></img>
                    <img className='photo' src ='2.jpg'></img>
                    <img className='photo' src ='3.jpg'></img>
                    <img className='photo' src ='4.jpg'></img>
                    <img className='photo' src ='5.jpg'></img>
                </div>
            </div>
        )

    }
}